.experiences_container{
    display:flex;
    flex-direction:column;
    gap:2rem;
}

.experiences__box{
    display:flex;
    align-items:center;
    gap:2rem;
}
.experiences__img{
    height:50px;
    width:50px;
    display:flex;
    align-items:center;
    justify-content:center;
    padding:.5rem;
}
.experiences__img img{
    width:100%;
    height:auto;
}
.experiences__box h5{
    color:#a0a0a0;
}
.experiences__box h4{
    display:flex;
    align-items: center;
    gap:1rem;
}
.experiences__box small{
    font-size:1rem;
    background:var(--color-bg);
    color:var(--color-primary);
    box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
    padding:.3rem .5rem;
    border-radius:5px;
}
/* ----------- MEDIA QUERIES (MD DEV) ------------- */
@media screen and (max-width:1024px){
    

}
/* ----------- MEDIA QUERIES (SM DEV) ------------- */
@media screen and (max-width:600px){
    .experiences__box small{
        font-size:.8rem; 
    }
}